<template>
  <div class="manage-photos">
    <div class="manage-photos-header">
      <PublicHead title="管理照片" :isBtn="false" />
    </div>
    <div class="view-method">
      <div class="view-name">
        按上传时间
      </div>
      <div class="view-icon">
        <img src="../../assets/img/staffOnly/btn_sorting.png" alt="">
      </div>
    </div>
    <div class="photos" v-infinite-scroll="loadMore" infinite-scroll-disabled="isMore" infinite-scroll-distance="5">
      <div class="photos-item" v-for="(item,index) in photosList" :key="index">
        <div class="photos-img">
          <img :src="item.url" alt="">
        </div>
        <div class="photos-right">
          <div class="photos-text">
            <div class="photos-name ellipse">
              {{item.name}}
            </div>
            <div class="photos-time">
              {{item.time}}
            </div>
            <div class="delete-photos" @click="delImgBtn(item._id,index)">
              <img src="../../assets/img/staffOnly/btn_delete.png" alt="">
              <span>删除</span>
            </div>
          </div>
          <div :class="[item.show == 1?'photos-show' : 'photos-hide']" @click="setImgShow(item)">
            <div class="photos-switch"></div>
            <div class="photos-eye"></div>
          </div>
        </div>
      </div>
      <!-- 下拉提示 -->
      <div class="course-down">
        <p v-show="tipsIndex === 1" class="course-down-tips">加载中，请稍后</p>
        <p v-show="tipsIndex === 2" class="course-down-tips">上拉加载更多</p>
        <p v-show="tipsIndex === 3" class="course-down-tips">已显示全部内容</p>
      </div>
    </div>
    <div class="footer-box"></div>
    <div class="footer">
      <div class="upload-photos-btn">
        上传照片
      </div>
    </div>
  </div>
</template>

<script>
import PublicHead from '../../components/publicHead.vue'
export default {
  data() {
    return {
      pageIndex: 0,  //当前页
      pageSize: 10,  //一页多少条   
      openid: '123456',  //用户id

      photosList: [],  //图片列表
      isMore: false,
      tipsIndex: 2,
    }
  },
  methods: {
    loadMore(){
      this.loading = true;
      this.tipsIndex = 1;
      this.pageIndex++

      this.getResource()
    },
    delImgBtn(id, index) {  //删除按钮
      this.$messagebox.confirm('确定执行此操作?').then(action => {

        this.delImg(id, index);
      }).catch(err => {
        console.log(err);
      })
    },
    async delImg(id, index) {  // 删除接口
      try {
        let res = await this.$api.staffOnly.delImg({ id });

        console.log(res);
        if (res.statusCode === 75200) {
          this.photosList.splice(index, 1)
        } else {
          this.$zero.Toast('服务器异常，请稍后重试');
        }
      } catch (err) {
        this.$zero.Toast('服务器异常，请稍后重试');
      }
    },
    async getResource() {
      try {
        let res = await this.$api.staffOnly.getResource({
          pageIndex: this.pageIndex,
          pageSize: this.pageSize,
          openid: this.openid
        })

        console.log(res);
        if (res.statusCode !== 75200) {
          return;
        }
        this.photosList.push(...res.list);
        if(res.list.length < this.pageSize){
          this.isMore = true;
          this.tipsIndex = 3;
        }else{
          this.isMore = false;
        }
        

      } catch (err) {
        console.log(err);
        this.$zero.Toast('服务器异常，请稍后重试');
        
      }
    },
    async setImgShow(item) {
      try {

        let res = await this.$api.staffOnly.setImgShow({
          // openid: this.openid,
          id: item._id,
          show: item.show == 1 ? 2 : 1
        })

        if (res.statusCode === 75200) {
          item.show = item.show == 1 ? 2 : 1;
        } else {
          this.$zero.Toast('服务器异常，请稍后重试');
        }

      } catch (err) {
        console.log(err);
        this.$zero.Toast('服务器异常，请稍后重试');
      }
    }
  },
  created() {
    // this.getResource();
  },
  components: {
    PublicHead
  }
}
</script>

<style scoped>
